import React from 'react';
import './Registration.css';
import Input from '../../UI/Input/Input';
import { Redirect} from 'react-router-dom';
import axios from 'axios';

class SignIn extends React.Component {
    state = {
        logedIn: false,
        signinForm: {
            name: {
                elementConfig: {
                    type: 'text',
                    placeholder: 'Your Name'
                },
                value: '',
                validation: {
                    required: true
                }, 
                // valid: false,
                // touched: false
            },
            lastName: {
                elementConfig: {
                    type: 'text',
                    placeholder: 'Your  Last Name'
                },
                value: '',
                validation: {
                    required: true
                }, 
                // valid: false,
                // touched: false
            },
            email: {
                elementConfig: {
                    type: 'email',
                    placeholder: 'your E-Mail'
                },
                value: '',
                validation: {
                    required: true
                }, 
                // valid: false,
                // touched: false
            },
            Password: {
                elementConfig: {
                    type: 'password',
                    placeholder: 'your Password'
                },
                value: '',
                validation: {
                    required: true
                }, 
                // valid: false,
                // touched: false
            },
        }
    }

    loginHandler =() => {
        this.setState({logedIn: true});
    }
 
    signinHandler = (event) => {
        event.preventDefault();
        const formData = {};
        for(let formElementIdentifier in this.state.signinForm) {
            formData[formElementIdentifier] = this.state.signinForm[formElementIdentifier].value;
        }
        const user = {
            userData: formData
        }
        axios.post('/users.json', user)
        .then(response => {
            this.props.history.goBack();
        })
        .catch( error => {
            console.log('something go wrong');
        });
    }

    inputChangedHandler = (event, inputIdentifier) => {
        const updatedSigninForm = {
            ...this.state.signinForm
        };
        const updatedFormElement = {
            ...updatedSigninForm[inputIdentifier]
        };
        updatedFormElement.value = event.target.value;
        updatedSigninForm[inputIdentifier] = updatedFormElement;
        this.setState({signinForm: updatedSigninForm})
    }

    render() {
        let redirectToLogin = null;
        if(this.state.logedIn){
            // redirectToLogin = <Redirect to='/login' />;
        }
        const formElementsArray = [];
        for( let key in this.state.signinForm ){
            formElementsArray.push({
                id: key,
                config: this.state.signinForm[key]
            });
        }
        let form = (
            <form onSubmit={this.signinHandler}>
                {formElementsArray.map(formElement => (
                    <Input 
                    key={formElement.id}
                    elementConfig={formElement.config.elementConfig}
                    value={formElement.config.value}

                    // invalid={!formElement.config.valid}
                    //     shouldValidate={formElement.config.validation}
                    //     touched={formElement.config.touched}
                    changed={(event) => this.inputChangedHandler(event, formElement.id)}
                    />)
                )}
                <button>SUBMIT</button>
            </form>
        )
        return(
            <div className={"RegisterBox"}>
                {redirectToLogin}
                <h2>SIGNIN</h2>
                {form}
                <button onClick={this.loginHandler} style={{backgroundColor: 'green'}}>LOGIN</button>             

            </div>
        );   
    }
}

export default SignIn;