import React from 'react';
import './Toolbar.css';
import { Route } from 'react-router-dom';
import NavigationItem from '../Navigation/NavigationItem/NavigationItem';
import Contact from './ToolbarProps/Contact';
import About from './ToolbarProps/About';
import HomePage from '../MainScreen/HomePage/HomePage';

class Toolbar extends React.Component{
    state = {
        navigation: {
            home: {
                name: 'Home',
                path: '/',
                exact: true
            },
            about: {
                name: 'About Us',
                path: '/aboutUs',
                exact: false
            }, 
            contact: {
                name: 'Contact Us',
                path: '/contact',
                exact: false
            }, 
        },
    }

    render(){
        const navigationsArray = [];
        for( let key in this.state.navigation ){
            navigationsArray.push({
                id: key,
                config: this.state.navigation[key]
            });
        }
        let navigation = (
            <ul>
                {navigationsArray.map(navigationItem => (
                    <NavigationItem 
                        key={navigationItem.id}
                        name={navigationItem.config.name}
                        path={navigationItem.config.path}
                        exact={navigationItem.config.exact}
                    />
                ))}
            </ul>
        );

        return(
            <header className={"Toolbar"}>
                <div className={"logo"}>TASTY FOODIE</div>
                <nav>{navigation}</nav>
                <Route path="/about" component={About}/>
                <Route path="/contact" component={Contact}/>
            </header>
        );
    }    
}

export default Toolbar;