import React from 'react';
import Box  from '../../../UI/Box/Box';
import './AppNavigator.css';
import MenuCategories from './ MenuCategories/MenuCategories';

import { Route, Redirect, Switch } from 'react-router-dom';


class AppNavigator extends React.Component{
    state = {
        navigator : {
            menu: {
                id: 1,
                name: 'Menu',
                color: 'red',
                isClicked: false
            },
            registration: {
                id: 2,
                name: 'Log-in / Sign-up',
                color: 'yellow',
                isClicked: false
            },
            vipClub :{
                id: 3,
                name: 'VIP Club',
                color: 'green',
                isClicked: false
            }
        },
    }

    onClickNavItem = (name) => {
        const updatedNavigator = {...this.state.navigator};
        if(name === this.state.navigator.menu.name){
            const menuNav = updatedNavigator.menu;
            menuNav.isClicked = true;
            updatedNavigator.menu = menuNav;
        }
        else if(name === this.state.navigator.registration.name){
            const registrationNav = updatedNavigator.registration;
            registrationNav.isClicked = true;
            updatedNavigator.registration = registrationNav;
        }
        else {
            const vipClubNav = updatedNavigator.vipClub;
            vipClubNav.isClicked = true;
            updatedNavigator.vipClub = vipClubNav;
        }
        this.setState({navigator: updatedNavigator});
    }


    render(){
        const navigationsArray = [];
        for(let key in this.state.navigator) {
            navigationsArray.push({
                id: key,
                config: this.state.navigator[key]
            });
        }
        let navigation = navigationsArray.map(navigationItem => (
            <Box 
                id={navigationItem.id}
                title={navigationItem.config.name}
                onClick={this.onClickNavItem.bind(this)}
                backgroundColor={navigationItem.config.color} 
            />
        ));

        let menuCategories = null;
        let redirectNav = null;
        if(this.state.navigator.menu.isClicked){
            menuCategories = <MenuCategories />;
        }
        if(this.state.navigator.registration.isClicked){
            redirectNav = <Redirect to='/registration'/>;
        }


        return(
            <div className={"AppNavigator"}>
                {navigation}
                {menuCategories}
                {redirectNav}
            </div>
        );
    }
}

export default AppNavigator;