import React from 'react';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import './Carousel.css';

import specials from './images/meal-plan-bloating-te-main2-200708_a44d97d033d32d93190bd1331593fca0.jpg';
import meals from './images/fideos-secos-tacos-FT-RECIPE0420-1.jpg';
import drinks from './images/caesar-drink-480x360.jpg';
import desserts from './images/whiskey-ice-cream-float-4-670x610.jpg';

const photos = [
    {
        name: 'specials',
        url: specials,
        alt: 'Specials'
    },
    {
        name: 'meals',
        url: meals,
        alt: 'Meals'
    },{
        name: 'drinks',
        url: drinks,
        alt: 'Drinks'
    },{
        name: 'desserts',
        url: desserts,
        alt: 'Desserts'
    }
];

class CarouselMenu extends React.Component{
    render(){
        const settings = {
            dots: true,
            fade: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1
        }
        return(
            <div className={"Carousel"}>
                <Slider {...settings}>
                    {photos.map((photo) => {
                        return(
                            <div>
                                <img width='100%' height='550px' src={photo.url} alt={photo.alt}/>
                            </div>
                        );
                    })}
                </Slider>
            </div>
        ); 
    }
}

export default CarouselMenu;